import { PageHeader } from "../../../../../../shared/PageHeader";
import { PageFooter } from "../../../../../../shared/PageFooter";
import { Dialog } from "primereact/dialog";
import { useRef, useState } from "react";
import { Toast } from "primereact/toast";
import { useTranslation } from "react-i18next";
import { ListOfVacationLimitsTable } from "../../../../../../features/table";
import { AddLimitForm } from "../../../../../../features/form";

export const Limits = ({ vacationTypes }) => {
  const { i18n, t } = useTranslation();

  const dialogState = useState({ visible: false });

  const [dialogStateValue, setDialogStateValue] = dialogState;
  const toastRef = useRef(null);

  return (
    <>
      <Dialog
        header={t(
          "pages.settings.organization.pages.vacations.limits.dialog.title"
        )}
        className="dialog"
        visible={dialogStateValue.visible}
        onHide={() => setDialogStateValue({ visible: false })}
      >
        <AddLimitForm
          i18n={i18n}
          toastRef={toastRef}
          dialogState={dialogState}
          employee={dialogStateValue.data}
          vacationTypes={vacationTypes}
        />
      </Dialog>
      <Toast ref={toastRef} />
      <main className="page settingsOrganizationVacationsLimits_page container_local">
        <PageHeader title={t("pages.settings.organization.pages.vacations.limits.header.title")}/>

        <div className="page_content">
          <ListOfVacationLimitsTable
            toastRef={toastRef}
            i18n={i18n}
            dialogState={dialogState}
            vacationTypes={vacationTypes}
          />
        </div>

        <PageFooter />
      </main>
    </>
  );
};
