import { useEffect, useState } from "react";
import { Limits as UI } from "./ui/Limits";
import { useTranslation } from "react-i18next";
import { Loading } from "../../../../../shared/Loading";
import { Error } from "../../../../../shared/Error";
import { api } from "../../../../../shared/api";
import { itemsPerPageForAllItems } from "../../../../../entities/itemsPerPageForAllItems";

export const Limits = () => {
  const [componentStatus, setComponentStatus] = useState({ status: "loading" });

  const { i18n } = useTranslation();

  useEffect(() => {
    (async () => {
      const vacationTypesResponse = await api.vacationTypes.get({
        itemsPerPage: itemsPerPageForAllItems,
      });

      const { warning, error } = vacationTypesResponse;

      if (error || warning) {
        setComponentStatus({
          status: "error",
          data: error || warning,
        });
        return;
      }

      setComponentStatus({ status: "success", data: vacationTypesResponse });
    })();
  }, []);

  if (componentStatus.status === "loading") return <Loading />;
  if (componentStatus.status === "error")
    return <Error error={componentStatus.data} />;
  if (componentStatus.status === "success")
    return (
      <UI i18n={i18n} vacationTypes={componentStatus.data["hydra:member"]} />
    );
};

export default Limits;
